*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --bg-body-light: #fff;
  --bg-btn-var1: #9bb537;
  --bg-btn-var2: transparent;
  --color-text-light: #050505;
  --color-text-desc-l: rgba(5, 5, 5, .8);
  --color-categories-text-l: rgba(5, 5, 5, .3);
  --color-input-placeholder-l: rgba(5, 5, 5, .5);
  --color-active-el: #9bb537;
  --color-btn-var1: #f8f8f8;
  --color-btn-var2: #050505;
  --border-input-l: 1px solid rgba(5, 5, 5, .2);
  --border-radius: 15px;
  --border-btn-var2: 1px solid #9bb537;
  --color-scrollbar-thumb-light: rgba(5, 5, 5, .18);
  --bg-body-dark: #1e1e1e;
  --color-text-d: #fff;
  --color-text-desc-d: rgba(255, 255, 255, .8);
  --color-categories-text-d: rgba(255, 255, 255, .3);
  --color-input-placeholder-d: rgba(255, 255, 255, .5);
  --border-input-d: 1px solid rgba(255, 255, 255, .2);
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
  --color-scrollbar-thumb-dark: rgba(255, 255, 255, .18);
}

@font-face {
  font-family: Inter;
  font-weight: 400;
  src: local(Inter), url("Inter-Regular.21fb113a.woff2") format("woff2"), url("Inter-Regular.aebce6b4.woff") format("woff"), url("Inter-Regular.573a4d5e.ttf") format("truetype");
}

@font-face {
  font-family: Inter;
  font-weight: 500;
  src: local(Inter), url("Inter-Medium.684a61f9.woff2") format("woff2"), url("Inter-Medium.1be2400d.woff") format("woff"), url("Inter-Medium.3b890b5a.ttf") format("truetype");
}

@font-face {
  font-family: Inter;
  font-weight: 600;
  src: local(Inter), url("Inter-SemiBold.dde948a5.woff2") format("woff2"), url("Inter-SemiBold.45db5cc6.woff") format("woff"), url("Inter-SemiBold.dc799cc6.ttf") format("truetype");
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-thumb {
  background: var(--color-scrollbar-thumb-light);
  border-radius: 12px;
}

body {
  color: var(--color-text-light);
  font-family: Inter, sans-serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
  display: block;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.btn {
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0);
  display: block;
}

.visibility-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

img {
  line-height: 0;
  display: block;
}

.section {
  padding-bottom: 80px;
}

.container {
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .section {
    padding-bottom: 100px;
  }
}

@media screen and (max-width: 767px) {
  .container {
    max-width: 375px;
    padding: 0 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding: 0 100px;
  }
}

.dark {
  background-color: var(--bg-body-dark);
  color: var(--color-text-d);
  transition: background-color 1.5s;
}

.dark .navigation-link, .dark .black-logo, .dark .card-heading, .dark .card-button, .dark .first-words, .dark .text, .dark .btn-h, .dark .submit-btn[disabled], .dark .order-modal-title, .dark .label-recipe-name, .dark .plus-button-svg {
  color: var(--color-text-d);
}

.dark .logo {
  text-shadow: 0 4px 4px rgba(255, 255, 255, .106);
}

.dark .create-order-link svg, .dark .open-modal-button svg {
  fill: var(--color-text-d);
}

.dark .order-modal-close-icon, .dark .mobile-menu .close-modal-button {
  fill: var(--color-btn-var1);
}

.dark .header-scroll .navigation-link, .dark .header-scroll .black-logo {
  color: var(--color-text-light);
  transition: color 2.5s;
}

.dark .header-scroll .create-order-link svg {
  fill: var(--color-text-light);
  transition: fill 2.5s;
}

.dark .order-modal {
  background-color: var(--color-btn-var2);
  border: var(--border-input-d);
}

.dark .order-form-input, .dark .order-form-comment {
  border: var(--border-input-d);
  color: var(--color-text-desc-d);
}

.dark .list-category-btns .category-btn {
  color: var(--color-categories-text-d);
}

.dark .all-categories-btn, .dark .create-recipe-btn {
  color: var(--color-input-placeholder-d);
  border: var(--border-input-d);
}

.dark .modal-content-create {
  background-color: var(--bg-body-dark);
}

.dark .input-create, .dark .ingredient-name, .dark .ingredient-amount, .dark .textarea-create {
  background-color: var(--bg-btn-var2);
  color: rgba(255, 255, 255, .5);
  border: 1px solid rgba(255, 255, 255, .2);
  outline: none;
}

.dark .input-create:focus, .dark .ingredient-name:focus, .dark .ingredient-amount:focus, .dark .textarea-create:focus {
  border: 1px solid var(--color-active-el);
}

.dark label, .dark select, .dark .card-rating, .dark .footer-text, .dark .reset-filter {
  color: var(--color-input-placeholder-d);
}

.dark select {
  background-color: var(--bg-btn-var2);
  border: var(--border-input-d);
}

.dark .icon-search, .dark .plus-button-svg {
  fill: var(--color-input-placeholder-d);
}

.dark .input-search {
  color: var(--color-input-placeholder-d);
  border: var(--border-input-d);
  background-color: rgba(0, 0, 0, 0);
}

.dark .input-search::-ms-input-placeholder {
  color: var(--color-input-placeholder-d);
  transition: none;
}

.dark .input-search::placeholder {
  color: var(--color-input-placeholder-d);
  transition: none;
}

.dark .card-block:after {
  background: linear-gradient(1deg, #050505 0%, rgba(5, 5, 5, 0) 100%);
}

.dark .card-description {
  color: var(--color-text-desc-d);
}

.dark .mobile-menu .mobile-menu-content, .dark .modal-content {
  background: var(--color-text-light);
  transition: background 2.5s;
}

.dark ::-webkit-scrollbar {
  width: 8px;
}

.dark ::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, .18);
  border-radius: 12px;
}

.dark .pag-btn-number, .dark .pag-btn-white {
  background-color: var(--bg-btn-var2);
  border: 1px solid var(--color-categories-text-d);
  color: var(--color-text-d);
}

.dark .pag-btn-left-icon {
  fill: rgba(255, 255, 255, .5);
}

.dark .close-create:hover {
  color: var(--color-btn-var1);
}

.dark .footer {
  background-color: var(--color-btn-var2);
  transition: background-color 1.5s;
}

.dark .favorites__filter-btn {
  background-color: var(--color-btn-var2);
  border: var(--border-input-d);
  color: var(--color-categories-text-d);
}

.dark .pag-btn-on-hover {
  background-color: var(--bg-btn-var1);
}

.dark .favorites__active-btn {
  color: var(--color-btn-var1);
  background-color: var(--color-active-el);
}

.dark option {
  background-color: var(--color-text-light);
  color: var(--bg-body-light);
}

body {
  background-color: var(--bg-body-light);
  transition: background-color 1.5s;
}

.mobile-menu .mobile-menu-content {
  background-color: var(--bg-btn-var1);
  transition: background-color 1.5s;
}

.order-modal-close-icon {
  fill: var(--color-btn-var2);
}

footer .create-order-link svg, .close svg {
  fill: var(--color-text-d);
  stroke: var(--bg-btn-var2);
}

input, textarea, select, .btn-h {
  background-color: var(--bg-btn-var2);
}

input:-webkit-autofill, select:-webkit-autofill, textarea:-webkit-autofill {
  -webkit-background-clip: text;
  -webkit-text-fill-color: var(--color-input-placeholder-l);
}

.footer {
  transition: background-color 1.5s;
}

.search-recipes__wrapp .sidebar {
  margin-bottom: 44px;
}

@media screen and (min-width: 768px) {
  .search-recipes__wrapp {
    gap: 32px;
    display: flex;
  }

  .search-recipes__wrapp .sidebar {
    margin-bottom: 0;
  }
}

@media screen and (min-width: 1280px) {
  .search-recipes__wrapp {
    gap: 64px;
  }
}

.tui-pagination {
  width: 100%;
  justify-content: center;
  display: flex;
}

.tui-page-btn {
  width: 44px;
  height: 44px;
  border: 1px solid var(--color-categories-text-l);
  color: var(--color-text-light);
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: 600;
  line-height: 122.222%;
  display: flex;
}

.tui-is-selected:active {
  background-color: var(--bg-btn-var1);
}

.tui-prev {
  margin-left: 8px;
  margin-right: 24px;
}

.tui-next {
  margin-left: 24px;
  margin-right: 8px;
}

.tui-first, .tui-prev {
  background-color: var(--color-text-light);
  border: var(--bg-btn-var2);
  border-color: rgba(0, 0, 0, 0);
  font-size: 0;
}

