.tui-next, .tui-last {
  transform: rotate(180deg);
}

.tui-page-btn.tui-is-selected {
  border-color: var(--bg-btn-var1);
  background-color: var(--bg-btn-var1);
}

.tui-next, .tui-last {
  background-color: var(--bg-btn-var1);
  border: var(--bg-btn-var2);
  font-size: 0;
}

.switch input {
  display: none;
}

.slider {
  width: 46px;
  height: 20px;
  background-color: #ccc;
  border-radius: 10px;
